.searchBlock{
    max-width: 300px;
    flex-grow: 1;
    margin-right: 10px;
    margin-bottom: 10px;
    padding: 20px;

    align-self:flex-start;
    background: #FFFFFF;
    box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
}

@media(max-width:1024px)
{
    .searchBlock{
        margin-right: 0px;
        max-width: 680px;
    }
}

